<template>
  <ContentCard :title="title" @go-back="$emit('go-back')">
    <!--  -->
    <el-button type="primary" @click="submitBtn"></el-button>
    <div class="form-legend-header">
      <i class="el-icon-edit"></i>
    </div>
    <el-form
      :model="formData"
      :rules="formRules"
      ref="form"
      label-width="100px"
      class="el-form-add"
    >
      <el-form-item label="" prop="deptName">
        <el-input v-model="formData.deptName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="deptTel">
        <el-input v-model="formData.deptTel" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="deptFax">
        <el-input v-model="formData.deptFax" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="remark">
        <el-input v-model="formData.remark" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="seq">
        <el-input v-model="formData.seq" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="pid">
        <el-select v-model="formData.pid" placeholder="">
          <el-option label="" value="0"/>
          <el-option
            v-for="item in pidList"
            :key="item.deptId+''"
            :label="item.deptName"
            :value="item.deptId"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="disabled">
        <el-radio v-model="formData.disabled" label="0"></el-radio>
        <el-radio v-model="formData.disabled" label="1"></el-radio>
      </el-form-item>
    </el-form>
  </ContentCard>
</template>

<script>
import _ from '../../utlis/gateschema'

export default {
  props: {
    editId: Number,
    default: null
  },

  data() {
    return {
      title: null,
      pidList: [],
      formData: {
        deptName: '',
        deptTel: '',
        deptFax: '',
        remark: '',
        seq: null,
        disabled: '0',
        pid: '0'
      },
      formRules: {
        deptName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptTel: [
          { required: true, message: '', trigger: 'blur' }
        ],
        deptFax: [
          { required: true, message: '', trigger: 'blur' }
        ],
        remark: [{ required: true, message: '', trigger: 'blur' }],
        seq: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },

  methods: {
    //
    submitBtn() {
      this.$refs.form.validate(valid => {
        if (valid) {
          const data = this.formData
          if (this.editId) {
            data.deptId = this.editId
          }
          this.$service.sysDeptSave(data, '').then(() => {
            this.$emit('open-list-page')
          })
        }
      })
    },
    getDepts() {
      return this.$service.getDepts().then(data => {
        this.pidList = data
      })
    },
    getEditData() {
      this.$service.sysDeptDetailInfo({ id: this.editId }).then(data => {
        Object.keys(this.formData).forEach(v => {
          if (v === 'disabled') {
            this.formData[v] = data[v] + ''
          } else {
            this.formData[v] = data[v]
          }
        })
      })
    }
  },

  created() {
    this.getDepts()
    if (this.editId) {
      this.title = ''
      this.getEditData()
    } else {
      this.title = ''
    }
  }
}
</script>

<style lang='stylus' scoped>
.el-input
  width: 200px
</style>